<template>
  <div>
      <h1></h1>
    <div align="right">
        <el-tag type="info"><router-link to="/buyer_index" style="color:#6C6C6C;text-decoration:none"></router-link></el-tag>
        <el-tag type="info" style="color:#6C6C6C;cursor:pointer">{{username}}</el-tag>
        <el-tag type="info" @click="logout()" style="color:#6C6C6C;cursor:pointer"></el-tag>
        <el-tag type="info"><router-link to="/finance" style="color:#6C6C6C;text-decoration:none"></router-link></el-tag>
        <el-tag type="info"><router-link to="/shopping_cart" style="color:#6C6C6C;text-decoration:none"></router-link></el-tag>
    </div>  
    <div>
        <div style="margin:20px">:<el-input v-model="notPurchaseGoodsInfo.title" placeholder="" :readonly="true"></el-input></div>
        <div style="margin:20px">:<el-input v-model="notPurchaseGoodsInfo.summary" placeholder="" :readonly="true"></el-input></div>
        <div style="margin:20px">:<el-input v-model="notPurchaseGoodsInfo.price" placeholder="" :readonly="true"></el-input></div>
        <div style="margin:20px">:<el-input v-model="notPurchaseGoodsInfo.content" placeholder="" :readonly="true"></el-input></div>
        <!-- <div><el-button @click="jumpSellerIndex()"></el-button><el-button @click="jumpEditGoodsInfo()"></el-button></div> -->
        <!-- id:{{goodsId}}<br/> -->
        <div>
        <el-button @click="jumpBuyerIndex()"></el-button>
        <el-button @click="dialogVisible=true"></el-button>
        <el-dialog title="" :visible.sync="dialogVisible" width="30%">
          <span>{{notPurchaseGoodsInfo.title}}</span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="addShoppingCart()"> </el-button>
          </span>
        </el-dialog>
        </div>
    </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      notPurchaseGoodsInfo:"" ,
      error : '',
      backEndHost:'http://127.0.0.1:9527/',
      goodsId: '',
      dialogVisible: false,
      username:''
    }    
  },
  created :
  function () {
    this.username = sessionStorage.username;
  },
  mounted : function(){
            
            this.goodsId = this.$route.params.goodsId;
            this.getGoodsInfoByGoodsId(this.goodsId);
        },
  methods: {
      getGoodsInfoByGoodsId: function (dataType) {
            this.$http.post('http://127.0.0.1:9527/goods/getGoodsInfoByGoodsId?goodsId=' + dataType, {
            }).then(function (res) {
              if (res.body.code != 0){
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
              }else{
                this.notPurchaseGoodsInfo = res.body.data;
              }  
              }),
              function () {
              this.$notify({
                title: '',
                duration: 3000,
                message: '',
                type: 'warning'
              });
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
            }
        },
        handleClose: function() {
        this.$confirm('').then(function (){
            this.addShoppingCart();
          })
          .catch({});
        },
        addShoppingCart: function () {
            this.$http.post('http://127.0.0.1:9527/shoppingCart/addShoppingCart', {
                "goodsId":this.goodsId
            }).then(function (res) {
              this.dialogVisible = false;
              this.$message({
                showClose: true,
                message: res.body.msg,
                type: 'success'
              }); 
              }),
              function () {
              this.$notify({
                title: '',
                duration: 3000,
                message: '',
                type: 'warning'
              });
              this.$message({
                showClose: true,
                message: '',
                type: 'warning'
              });
            }
        },
        logout (){
        sessionStorage.username = '';
        this.$router.push({
            name:'user_login'
            });
        },
        jumpBuyerIndex (){
          this.$router.push({
            name:'buyer_index'
            });
        }
  }

}
</script>


<style scoped>
.el-input {
    width: 200px;
    height: 30px;
}
.el-select #inputContent {
        width: 425px;
        height: 100px;
    }
#cancel {
    margin-left: 100px;
  }
.el-select {
  width: 200px;
  height: 30px;
}
</style>